<template>
  <div>
    <b-table :items="resList"></b-table>
  </div>
</template>
<script>
import Axios from "axios";
import Tempaltes from "../templates";

export default {
  name: "TabTwo",
  data() {
    return {
      resList: [],
    };
  },
  async mounted() {
    await this.getTemplates();
  },
  methods: {
    async getData() {
      let x = Math.round(Math.random() * 100);
      var response = await Axios.get(`https://dummyjson.com/products/${x}`);
      this.resList.push(response.data);
    },
    async getTemplates() {
      this.resList = [...this.resList, ...Tempaltes.templates];
    },
  },
};
</script>
