<template>
  <div>
    <b-card
      v-for="(item, index) in resList"
      :key="index"
      class="mb-2"
      style="max-width: 20rem; cursor: pointer"
      @click="showCategory(item)"
    >
      {{ item.category }}
    </b-card>
    <b-sidebar id="category-sidebar" right shadow width="70%">
      <div>{{ categoryData.category }}</div>
      <div>{{ categoryData.name }}</div>
      <div>{{ categoryData.image }}</div>
      <div>{{ getWorkflow }}</div>
    </b-sidebar>
  </div>
</template>
<script>
import Axios from "axios";
import Tempaltes from "../templates";

export default {
  name: "TabThree",
  data() {
    return {
      resList: [],
      categoryData: {},
      categoriesList: [],
    };
  },
  async mounted() {
    await this.getTemplates();
  },
  computed: {
    getWorkflow() {
      if (this.categoryData.workflow)
        return JSON.parse(this.categoryData.workflow);
      else return "";
    },
  },
  methods: {
    async getData() {
      let x = Math.round(Math.random() * 100);
      var response = await Axios.get(`https://dummyjson.com/products/${x}`);
      this.resList.push(response.data);
    },
    async getTemplates() {
      this.resList.forEach((item) => {
        if (this.categoriesList.includes(item.category)) return;
        this.categoriesList.push(item.category);
      });
      this.resList = Tempaltes.templates;
    },
    showCategory(item) {
      this.categoryData = item;
      console.log("item", item);
      this.$root.$emit("bv::toggle::collapse", "category-sidebar");
    },
  },
};
</script>
