<template>
  <div>
    <b-table :items="resList" :fields="fields"></b-table>
  </div>
</template>
<script>
import Axios from "axios";
import Tempaltes from "../templates";

export default {
  name: "TabOne",
  data() {
    return {
      fields: [
        { key: "category", label: "Category" },
        { key: "name", label: "Name" },
        { key: "image", label: "Image" },
        { key: "workflow", label: "Workflows" },
      ],
      resList: [],
    };
  },
  async mounted() {
    console.log("mounted");
    await this.getTemplates();
  },
  methods: {
    async getData() {
      let x = Math.round(Math.random() * 100);
      var response = await Axios.get(`https://dummyjson.com/products/${x}`);
      this.resList.push(response.data);
    },
    async getTemplates() {
      this.resList = [...this.resList, ...Tempaltes.templates];
    },
  },
};
</script>
