<template>
  <div>
    <b-tabs content-class="mt-3">
      <template #tabs-end>
        <b-nav-item href="#" role="presentation" @click="changeRoute()"
          >Open Route</b-nav-item
        >
      </template>
      <b-tab title="Tab 0" active>
        <b-card>Welcome to Project</b-card>
      </b-tab>
      <b-tab title="Tab 1" @click="() => $refs.tab_one.getTemplates()"
        ><p><TabOne ref="tab_one" /></p
      ></b-tab>
      <b-tab title="Tab 2" @click="() => $refs.tab_two.getTemplates()"
        ><p><TabTwo ref="tab_two" /></p
      ></b-tab>
      <b-tab title="Tab 3" @click="() => $refs.tab_three.getTemplates()"
        ><p><TabThree ref="tab_three" /></p
      ></b-tab>
    </b-tabs>
  </div>
</template>

<script>
import TabOne from "../tabs/TabOne.vue";
import TabTwo from "../tabs/TabTwo.vue";
import TabThree from "../tabs/TabThree.vue";
export default {
  name: "HelloWorld",
  components: { TabOne, TabTwo, TabThree },
  props: {
    msg: String,
  },
  data() {
    return {
      templates: null,
    };
  },
  mounted() {},
  methods: {
    async getTemplates() {
      let { default: Templates } = await import("../templates");
      this.templates.push(Templates.templates);
      Templates = null;
    },
    destroyTemplates() {
      this.templates = null;
    },
    changeRoute() {
      const routeData = this.$router.resolve({ path: "/hello" });
      window.open(routeData.href, "_blank");
    },
  },
};
</script>

